.Direction {
  color: rgba(30, 30, 30);
  background-color: #f1f1f1;
  margin: 10px;
  padding: 20px;
  font-size: 30px;
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: lightcyan;
}

@media (max-width: 700px) {
  .Direction {
    text-align: center;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
  }
}
