.sb-1 {
}

.ss-1 {
  float: right;
}

.sd-1 {
  display: flex;
  flex-direction: row;
}

.sd-2 {
  border: 1px solid rgb(200, 200, 200);

  padding: 10px;
  min-width: 350px;
  width: 40%;
}
.sd-3 {
  display: flex;
  align-items: flex-end;
  width: 40%;
}
.sd-3 button {
  margin-left: 30px;
}
.sb-1,
.sb-2 {
  width: 80px;
}

.sd-1 button {
  width: 50px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
}

.hp {
  color: #17a2b8;
  font-size: 20px;
  margin-bottom: 20px;
}

.hps {
}
.hpss {
  font-size: 15px;
}
.gd-1 {
  margin-top: 70px;
  text-align: center;
}
.gb-1 {
  margin-right: 5px;
  float: left;
  color: black;
  border: none;
  cursor: pointer;
}
.gb-2 {
  float: right;
  color: black;
  border: none;
  cursor: pointer;
}
@media (max-width: 700px) {
  .sd-1 {
    display: flex;
    flex-direction: column;
  }
  .sd-2 {
    width: 100%;
    min-width: 0;
  }
  .ss-1 {
    float: none;
  }
  .ss-2 {
    float: right;

    display: block;
  }
  .ss-3 {
    display: block;
    float: right;
  }
  .sp-1 {
    max-width: 370px;
    height: 60px;
    min-width: 250px;
  }
  .sp-2 {
    max-width: 370px;
    height: 35px;
    min-width: 250px;
  }
  .sb-1,
  .sb-2 {
    width: 140px;
  }
  .sd-3 {
    display: block;

    width: 100%;
  }
  .sd-3 button {
    margin-left: 0;
    margin-top: 10px;
    width: 100%;
  }
  .hpss {
    display: block;
  }
  .hp {
    padding-bottom: 10px;
  }
}
