/* Landing Page */
.landing {
  width: 100%;
  min-width: 1200px;

  background-color: antiquewhite;
  display: flex;
  flex-direction: row;
  align-items: start;
  justify-content: flex-start;
}

@media (max-width: 700px) {
  .landing {
    min-width: 0;
    flex-direction: column;
    /* height: 90vh; */
    /*   landing的内容物如果overflow对下面的posts不构成影响，会重叠。*/
  }
}
