@media (min-width: 699px) {
  .imgwrapper {
    width: 50%;
    min-width: 650px;

    min-height: 500px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .imgwrapper img {
    width: 600px;
    height: 400px;
  }
  .image-gallery {
    width: 80%;
    color: #fff;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    justify-content: center;
    display: block;
    position: relative;
  }
}

@media (max-width: 700px) {
  .imgwrapper {
    width: 100%;
    min-width: 0;
  }
  .imgwrapper img {
    width: 300px;
    height: 300px;
  }
  .image-gallery {
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
  }
}
