#Directions {
  color: #fff;

  width: 50%;

  padding-top: 3%;
  padding-bottom: 3%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (max-width: 700px) {
  #Directions {
    width: 100%;
    height: 500px;
    flex-direction: column;
    padding-top: 3%;
    padding-bottom: 3%;
  }
}
