/* Posts Page */
.post-form .post-form-header {
  background: var(--primary-color);
  padding: 0.5rem;
}

.post1 {
  display: grid;
  grid-template-rows: 3fr 1fr;
  grid-gap: 2rem;
  align-items: center;
  padding-bottom: 0;
}

.post1 > div:first-child {
  text-align: center;
}

.post1 img {
  width: 600px;
  height: 400px;
}
/* 重要。 */

.post1 .comment-count {
  background: var(--light-color);
  color: var(--primary-color);
  padding: 0.1rem 0.2rem;
  border-radius: 5px;
  font-size: 0.8rem;
}

.post1 .post-date {
  color: #aaa;
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
}

@media (max-width: 700px) {
  .post1 {
    grid-template-columns: 2fr;
    grid-gap: 0.5rem;
  }

  .post1 a {
    padding: 0.3rem 0.4rem;
  }
  .post1 button {
    padding: 0.3rem 1rem;
  }

  .post1 img {
    width: 300px;
    height: 200px;
  }
  .bs-1 {
    display: block;
  }
}
